.terminal-box {
  font-family: monospace;
  font-weight: 500;
  overflow: auto;
  height: 35rem;
  width: 100%;
  color: rgb(103, 255, 15);
  background-color: black;
  font-size: 100%;
}

.terminal-child {
  padding: 0.1rem;
  padding-left: 1rem;
  padding-top: 0;
  color: #57fc14;
}

.grid-container {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
}

.animation-text {
  -webkit-animation: animate 2s linear forwards;
          animation: animate 2s linear forwards;
  color: #dbf227;
}

@-webkit-keyframes animate {
  0% {
    width: 0px;
    height: 0px;
  }
  30% {
    width: 50px;
    height: 0px;
  }
  60% {
    width: 50px;
    height: 80px;
  }
}

@keyframes animate {
  0% {
    width: 0px;
    height: 0px;
  }
  30% {
    width: 50px;
    height: 0px;
  }
  60% {
    width: 50px;
    height: 80px;
  }
}

#terminal {
  scrollbar-gutter: stable;
  width: 70vw;
  height: 65vh;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5);
  border-radius: 2rem;
  background-color: rgba(58, 7, 42, 1);
}

#terminal__body::-webkit-scrollbar {
  background-color: rgb(118, 3, 107);
  width: 0.7em;
  scrollbar-gutter: stable both-edges;
}

#terminal__body::-webkit-scrollbar-thumb {
  background-color: rgb(87, 46, 91);
  outline: 1px solid slategray;
  color: #3c3b37;
  width: 0.5em;
  border: 5px solid transparent;
  border-radius: 6.25rem;
  scrollbar-gutter: stable;
  background-clip: content-box;
}

#terminal__body::-webkit-scrollbar-track {
  border-radius: 6.25rem;
  scrollbar-gutter: stable;
  box-shadow: inset 0 0 0.6rem rgba(0, 0, 0, 0.3);
}

#terminal__bar {
  display: flex;
  width: 100%;
  height: 30px;
  align-items: center;
  padding: 0 8px;
  box-sizing: border-box;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: linear-gradient(#504b45 0%, #3c3b37 100%);
}

#bar__buttons {
  display: flex;
  align-items: center;
}

.bar__button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin-right: 5px;
  font-size: 8px;
  height: 12px;
  width: 12px;
  box-sizing: border-box;
  border: none;
  border-radius: 100%;
  background: linear-gradient(#7d7871 0%, #595953 100%);
  text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.2);
  box-shadow: 0px 0px 1px 0px #41403a, 0px 1px 1px 0px #474642;
}
.bar__button:hover {
  cursor: pointer;
}
.bar__button:focus {
  outline: none;
}
#bar__button--exit {
  background: linear-gradient(#f37458 0%, #de4c12 100%);
  background-clip: padding-box;
}

#bar__user {
  color: #d5d0ce;
  margin: 0rem 0rem 0rem 2rem;
  font-size: 14px;
  line-height: 15px;
}

#terminal__body {
  scrollbar-gutter: stable;
  overflow-y: auto;
  background: rgba(56, 4, 40, 0.9);
  font-family: "Ubuntu Mono";
  height: calc(100% - 29px);
  width: 100%;
  padding-top: 2px;
  margin-top: -1px;
  /* border-radius: 0 0 1rem 1rem; */
}

#terminal__prompt {
  padding-left: 0.625rem;
  display: flex;
  align-items: center;
}
#terminal__prompt--user {
  color: #7eda28;
}
#terminal__prompt--location {
  color: #4878c0;
}
#terminal__prompt--bling {
  color: #dddddd;
}
#terminal__prompt--cursor {
  display: block;
  height: 17px;
  width: 8px;
  margin-left: 9px;
  -webkit-animation: blink 1200ms linear infinite;
          animation: blink 1200ms linear infinite;
  -webkit-transform: translateX(-0.5rem);
          transform: translateX(-0.5rem);
}

@-webkit-keyframes blink {
  0% {
    background: #ffffff;
  }
  49% {
    background: #ffffff;
  }
  60% {
    background: transparent;
  }
  99% {
    background: transparent;
  }
  100% {
    background: #ffffff;
  }
}

@keyframes blink {
  0% {
    background: #ffffff;
  }
  49% {
    background: #ffffff;
  }
  60% {
    background: transparent;
  }
  99% {
    background: transparent;
  }
  100% {
    background: #ffffff;
  }
}

.terminal-input {
  outline: none;
  color: white;
  background-color: inherit;
}

.grid-separate {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
}
.output-image img {
  padding: 1rem;
  width: 100%;
  height: 100%;
}

.output-text {
  padding: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.output-header {
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  grid-template-rows: auto;
  padding: 1rem;
}

.output-container {
  padding: 2rem;
}

.output-content {
  padding: 2rem;
}

.back-button {
  display: flex;
  justify-content: center;
  align-items: center;
}

.output-header-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

